.renter-register-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #fff;
    font-family: 'Century Gothic', sans-serif;
}
  
.renter-register-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
  
.form-content {
    width: 50%;
}
  
.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}
  
.logo img {
    max-width: 100%;
    max-height: 100%;
}
  
h2 {
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: center;
}
  
p {
    margin-bottom: 20px;
    text-align: center;
}
  
.form-group {
    margin-bottom: 15px;
}
  
.form-group label {
    display: block;
    margin-bottom: 5px;
    text-align: left;
}
  
.form-group input,
.form-group select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box;
}
  
.btn {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #f00;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
  
.btn:hover {
    background-color: #c00;
}
  
.link {
    color: #007bff;
    text-decoration: none;
}
  
.link:hover {
    text-decoration: underline;
}
